<template>
  <div class="FQAPage">
	<div class="fqa_content">
		<h1></h1>
		<div class="question_content">
			<ul>
				<li>
					<div class="one_wrap"><p  class="one_p"></p></div>
					<div class="two_wrap">
						<p></p>
					</div>
				</li>
				<li>
					<div class="one_wrap"><p class="one_p"></p></div>
					<div class="two_wrap">
						<p>
                        30</p>
					</div>
				</li>
				<li>
					<div class="one_wrap"><p class="one_p"></p></div>
					<div  class="two_wrap">
						<p>+
                        </p>
					</div>
				</li>
				<li>
					<div class="one_wrap"><p class="one_p"></p></div>
					<div class="two_wrap">
						<p>
                        100/
                        </p>
					</div>
				</li>
				<li>
					<div class="one_wrap"><p class="one_p"></p></div>
					<div class="two_wrap">
						<p>
                        </p>
					</div>
				</li>
			</ul>
		<!-- 	<div class="bottom"><a href="">&gt;&gt;&gt;</a></div> -->
		</div>

	</div>
  </div>
</template>

<script>
export default {
  name: 'FQAPage',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  mounted:function(){
  	// $(".question_content li").unbind();
  	$(".question_content li").click(function(){
  		$(this).find(".one_p").addClass("active")
  	    $(this).find(".two_wrap p").show();
  	    $(this).siblings().find(".two_wrap p").hide();
  	    $(this).siblings().find(".one_p").removeClass("active");
  	});
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.FQAPage{
	background-color: #fafafa;
}
.FQAPage .fqa_content{
	width: 1200px;margin: 0 auto;
}
.FQAPage .fqa_content h1{
	text-align: center;
	font-weight: normal;
	font-size: 24px;
	line-height: 24px;
	padding:22px 0;
    color: #ea5413;
}
.FQAPage .fqa_content .question_content .one_p{
	font-size: 14px;
	color: #333333;
	background:url(./picture/blue.png) no-repeat;
	width: 520px;
	height: 32px;line-height: 32px;
	padding-left: 34px;
	margin-left: 25px;
    background-position: 2px;
    cursor: pointer;
}
.FQAPage .fqa_content .question_content .one_p.active{
	background:url(./picture/orange.png) no-repeat;
    background-position-y: 5px;
}
.FQAPage .fqa_content .question_content .one_wrap{
	background-color: #dddddd;
	width: 520px;
	margin-bottom: 10px;
}
.FQAPage .fqa_content .question_content .two_wrap p{
	display: none;
	width: 498px;
    line-height: 30px;
    font-size: 16px;
    color:#333;
    margin: 12px 0;
    padding-left: 22px;

}
.FQAPage .fqa_content .question_content {
	margin-left: 340px;
	padding-bottom: 40px;
}
.FQAPage .fqa_content .question_content .bottom a{
	color: #5db8d8;
	font-size: 14px;
	width: 520px;
	text-align: right;
    display: block;
}

</style>



